/* 最大宽度 */
.page {
  padding: 32px;
  text-align: center;
}
/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}
.sidebar-header .icon {
  color: #2f8cf0;
}
.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;
}
.sidebar-header-more::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-top: 1px solid #D9D9D9;
  border-right: 1px solid #D9D9D9;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.sidebar-header-more:hover {
  color: #2d8cf0;
  text-decoration: underline;
}
.v-note-wrapper {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}
.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}
.comment-header {
  font-size: 28px;
  font-weight: 500;
  color: #2d8cf0;
  padding: 32px 0 16px;
}
.comment-item {
  margin: 16px 0;
  display: flex;
}
.comment-avatar {
  margin-right: 16px;
}
.comment-username {
  color: #17233d;
  font-size: 18px;
  font-weight: 500;
}
.comment-info {
  flex: 1;
}
.comment-content {
  color: #515a6e;
  line-height: 36px;
  font-size: 16px;
}
.comment-reply {
  padding: 12px 24px;
  margin: 24px 0;
  border-radius: 6px;
  background: #f8f8f8;
}
.comment-reply-btn {
  color: #2d8cf0;
  font-size: 16px;
  cursor: pointer;
}
.time {
  font-size: 14px;
  font-weight: bold;
}
.content {
  padding-left: 5px;
}
.load-more-comment {
  text-align: center;
  margin: 32px 0;
  font-size: 18px;
  color: #2d8cf0;
  cursor: pointer;
}
