/* 最大宽度 */
@window-Width: 70%;

.page {
  padding: 32px;
  text-align: center;
}

/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}

.sidebar-header .icon {
  color: #2f8cf0;
}

.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  &:hover {
    color: #2d8cf0;
    text-decoration: underline;
  }
}

.v-note-wrapper {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}

.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}








































































.sidebar {
  width: 350px;
}

@media screen and (min-width: 200px) and (max-width: 750px) {
  .sidebar {
    display: none;
  }
}

.sidebar-box {
  margin-bottom: 32px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 2px 3px #f0f0f0;
  background: #fff;
}

.sidebar-box-content {
  padding: 0 32px;
}

.sidebar-box-content-item {
  cursor: pointer;
  display: flex;
  margin: 24px 0;
  align-items: center;
  justify-content: space-between;
  color: #657180;

  &:hover {
    color: #2d8cf0;
  }
}

.sidebar-box-content-item h1 {
  font-size: 16px;
  font-weight: 400;
}

.sidebar-box-content-item a {
  flex: 1;
  color: #657180;
  margin-right: 10px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;

  &:hover {
    color: #2d8cf0;
  }
}

.sidebar-box-content-item span {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-top: 1px solid #D9D9D9;
  border-right: 1px solid #D9D9D9;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.icon {
  margin-right: 10px;
}
